				if(no.length==2){
					this.lists=JSON.parse(JSON.stringify(this.list))
				}else if(no[0]=='one'){
					this.lists=this.list.filter(function(a,b,c){
						return a.bitd=='游戏杂谈'
					})
				}else if(no[0]=='two'){
					this.lists=this.list.filter(function(a,b,c){
