					})
					this.le=1
				}
			},
			sho:function(){
				this.show_a=false;
			},
			ch:function(aa){
				this.leng=0;
				this.it=this.bot_lis[aa]
				this.show_a=false;
				if(aa==1){
					this.lists=JSON.parse(JSON.stringify(this.list))
					this.lists.sort(function(a,b){
					return b.bitc-a.bitc
				})
				}else if(aa==2){
					this.lists=JSON.parse(JSON.stringify(this.list))
					this.lists.sort(function(a,b){
					return b.pl-a.pl
				})
				}else{
					this.lists=JSON.parse(JSON.stringify(this.list))
				
				}
				
			}
