<template>
	<div class="whole">
		<ac-header></ac-header>
		<div class="ac_main clearfix">
		<ac-left></ac-left>
		<ac-right></ac-right>
		</div>
	</div>
</template>

<script>
	import header from './top.vue'
	import left from './left.vue'
	import right from './right.vue'
	export default{
		components:{
			'ac-header':header,
			'ac-left':left,
			'ac-right':right,
		}
	}
</script>

<style>
.ac_main{
	position: relative;
	width: 1240px;
	margin: 92px auto 0 auto;
}
</style>


